*{
    text-decoration: none;
    font-family:helvetica;
    
}

  
  .tooltip .tooltiptext {
    visibility: hidden;
   
  
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }

.margins{
    margin-top:100px;
    width:60vw;
    margin-left:auto;
    margin-right:auto;
   
}
@media (max-width:900px) {
    .margins{width:85%} 
 }
 

@media (max-width:700px) {
   .margins{width:95%} 
}

.center{
  
    text-align:center;

}
.mtop{
    margin-top:100px;
    font-family: monospace;

   
}

#main{
    display:grid;
    grid-template-columns: auto auto;
    background:url("https://previews.123rf.com/images/bbbaaaaz/bbbaaaaz1610/bbbaaaaz161000056/66073840-modern-background-with-particles-style-perfect-for-website-or-banner-backgrounds-or-as-a-wallpaper.jpg");
}
@media(max-width:700px){
    #main{
        display:grid;
        grid-template-columns:auto;
    }
}
iframe{
    width:60%;
    height:40vh;
    
    overflow: hidden;
    position: relative;
}
@media(max-width:700px){
    iframe{
        width:90%;
        height:70vh;
    }
}
